.stars {
  display: flex;

  &__star {
    flex-shrink: 0;
    margin-right: 4px;
    box-sizing: border-box;
    width: 16px;
    height: 16px;
    background: url(../images/star.svg) no-repeat center;
  }

  &__star:last-child {
    margin-right: 0;
  }
}

@for $stars from 0 through 5 {
  .stars--#{$stars} > .stars__star:nth-child(-n + #{$stars}) {
    background-image: url(../images/star-active.svg);
  }
}
